

function getSysType() {
  let u = navigator.userAgent,
    app = navigator.appVersion;
  let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
  let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
  if (isAndroid) {
    return 'android'
  }
  if (isIOS) {
    return 'ios'
  }
}

//uaapp
function isWDPHAPP(appName = "wdph") {
  let ua = navigator.userAgent.toLowerCase()
  let startIndex = ua.indexOf(appName)
  console.log('isWDPHAPP',startIndex)
  if (startIndex === -1) {
    return false
  } else {
    return true
  }
}


/**
 * lzsheng 2018-03-22
 * UserAgentAPP(APP>=2.1.1UserAgentAPP)
 * @param string WDPH
 * @return string
 * : WDPH/2.9.3
 */
function getAppVersionFromUserAgent(appName = "WDPH") {
  let ua = navigator.userAgent
  let startIndex = ua.indexOf(appName)
  if (startIndex == -1) { return false }
  let endIndex = ua.indexOf(" ", startIndex)
  let wdphUa = ""
  if (endIndex > -1) {
    wdphUa = ua.substr(startIndex, endIndex - startIndex)
  } else {
    wdphUa = ua.substr(startIndex)
  }

  startIndex = wdphUa.indexOf('/')
  let wdph_version = ''
  if (endIndex > -1) {
    wdph_version = wdphUa.substr(startIndex + 1, endIndex - startIndex - 1)
  } else {
    wdph_version = wdphUa.substr(startIndex + 1)
  }

  return String(wdph_version)
}


export default {
  getSysType,
  isWDPHAPP,
  getAppVersionFromUserAgent
}



