import React, { Component } from 'react'
import { HashRouter as Router, Route } from 'react-router-dom'
import PropTypes from 'prop-types'

import MgmIndex from './component/Index/'

import './style.scss'

class MgmRouters extends Component {

  render() {
    return (
      <Router basename='/mgm'>
        <div className='mgm'>
          {/*  */}
          <Route path='/' component={MgmIndex} />
        </div>
      </Router>
    )
  }
}

export default MgmRouters



