//webpack
let _env = process.env.WD_ENV

// _envByHostname hostnameprocess.env.WD_ENV
let _envByHostname
function hostname2env() {
  _envByHostname = {
    '10.53.145.194:16020': 'dev',
    '10.214.169.125:8081': 'sit',
    '10.214.169.165:8069': 'uat',
    'uat.wandaph.com': 'uat',
    'www.wandaph.com': 'pro',
  }[window.location.host]
}
hostname2env()
_env = _envByHostname ? _envByHostname : _env

console.log('_env', _env)

function initGlobal() {
  /*
  * BaseURL           
  * LocalURL          URL
  * DefaultChannel    
  * skip_alipay       
  * skip_white        
  * popupArray        APP
  * sign                sit: default    pro:productiont
  */

  let toGlobal = {
    BaseURL: 'http://10.53.145.194:16020/loan-web-deploy',//dev
    LocalURL: 'http://10.53.145.194:16020/h5',//dev
    sign: 'default',//dev,sit,uat
    DefaultChannel: '7BFD355B3D0E4137B2ED82AEFF410FA0',//sit
    channelId: '',//default

    //config.js
    skip_alipay: true,//default
    skip_white: true,//default
    popupArray: ['7BFD355B3D0E4137B2ED82AEFF410FA0'],//sit
  }

  switch (_env) {
    case 'sit':
      toGlobal.BaseURL = 'http://10.214.169.125:8081/loan-web-deploy'; // sit
      toGlobal.LocalURL = 'http://10.214.169.125:8081'; //sit
      break;
    case 'uat':
      toGlobal.BaseURL = 'http://10.214.169.165:8069/loan-web-deploy'; // uat
      toGlobal.LocalURL = 'http://10.214.169.165:8069/static/html'; //uat
      break;
    case 'pro':
      toGlobal.BaseURL = 'https://app.wandaph.com';  //pro
      toGlobal.LocalURL = 'https://www.wandaph.com'; //pro
      toGlobal.sign = 'productiont';//
      toGlobal.DefaultChannel = '675242361C8F4ABB95EC08DDE5CB1909'; //ID
      toGlobal.popupArray = [
        'co_qf_bt_ban0001',
        'co_qf_bt_ban0002',
        'co_qf_bt_ban0003',
        'co_qf_bt_ban0004',
        'co_qf_bt_ban0005',
        'co_qf_bt_ban0006',
        'co_qf_bt_ban0007',
        'co_qf_bt_ban0008',
        'co_qf_bt_ban0009',
        'co_qf_bt_ban0010'
      ];
      toGlobal.skip_alipay = false;//pro
      toGlobal.skip_white = false;//pro
      break;
    default:
      break;
  }

  let showWindowObj = {}
  for (const key in toGlobal) {
    if (toGlobal.hasOwnProperty(key)) {
      window[key] = toGlobal[key];
      showWindowObj[key] = toGlobal[key]
    }
  }

  _env !== "pro" && console.log('initGlobal to add "window" prop', showWindowObj)

}

function init() {
  initGlobal()
}

export default init



