
import utils from '../utils'

/**
 * @name{string} 
 * @param{object} 
 * @successCallback{function|string} ,APP
 * @failCallback{function|string} ,APP
 */
function toCall(name, param = {}, successCallback, failCallback) {

  if (!utils.isWDPHAPP()) {
    console.log("APP")
    return false
  }

  var android_jsBridge_Name = 'jsBridge_WDPH'

  //test
  // window[android_jsBridge_Name] = {}
  // window[android_jsBridge_Name][name] = function () {

  // }

  var timestamp = new Date().getTime()

  var successCB_NAME = 'WDPH_APP_CB_SUCCESS_' + timestamp
  var failCB_NAME = 'WDPH_APP_CB_FAIL_' + timestamp

  window[successCB_NAME] = function (...params) {
    //
    console.log(successCallback)
    typeof successCallback === 'function' && successCallback(...params)
    //window
    typeof successCallback === 'string' && typeof window[successCallback] === 'function' && window[
      successCallback](...params)
  }

  window[failCB_NAME] = function (...params) {
    //
    typeof failCallback === 'function' && failCallback(...params)
    //window
    typeof failCallback === 'string' && typeof window[failCallback] === 'function' && window[failCallback](
      ...params)
  }

  var system = utils.getSysType()

  console.log('=======')

  console.log('window:', successCB_NAME, failCB_NAME)

  if (system === 'ios') {
    //ios

    console.log('' + system + ':' + 'window.webkit.messageHandlers' + '.' + name)

    param = Object.assign({}, param, { iOS_ACTION: name })

    window.webkit.messageHandlers[name].postMessage({
      params: JSON.stringify(param),
      successCallbackFuncName: successCB_NAME,
      failCallbackFuncName: failCB_NAME
    })

  } else if (system === 'android') {
    //android
    if (typeof window[android_jsBridge_Name] !== 'undefined') {
      console.log(window[android_jsBridge_Name])

      console.log('' + system + ':' + android_jsBridge_Name + '.' + name)

      window[android_jsBridge_Name][name](
        JSON.stringify(param),
        successCB_NAME,
        failCB_NAME
      )

    } else {
      console.error('window' + android_jsBridge_Name)
    }
  }
}

export default {
  toCall,
}



