import 'core-js/fn/object/assign'

import init from './bootstrap'
init()

import React, { Component } from 'react'
import ReactDom from 'react-dom'
import {
  HashRouter as Router,
  Route,
  Link,
} from 'react-router-dom'

import 'PATH_SCSS/reset/_reset.scss'

import DocRouters from './views/doc'
import MgmRouters from './views/mgm'


class Root extends Component {
  constructor(props) {
    super(props)
  }
  render() {
    return (
      <Router basename='/'>
        <div className='appview'>
          <Route path='/doc' component={DocRouters} />
          <Route path='/mgm' component={MgmRouters} />
        </div>
      </Router>
    );
  }
}

ReactDom.render(
  <Root />,
  document.getElementById('main')
)

//
if (module.hot) {
  module.hot.accept()
}



